import Vue from "vue";
import Vuex from "vuex";
import axios from "axios";
import moment, { Moment } from "moment";
import * as types from "../types";
import utils from "../utils";
import * as content from "../assets/content.json";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    appState: "unloaded",
    locale: "en-mx",
    text: {},
    mapState: "unloaded",
    tableState: "unloaded",
    countType: "numbers",
    caseType: "positivo",
    totals: {
      mx: {
        positivo: 0,
        negativo: 0,
        finado: 0,
      },
      entidades: {},
    },
    trends: {
      mx: {
        positivo: [],
        negativo: [],
        finado: [],
      },
      entidades: [],
    },
    activeStates: [],
    dateRange: {
      min: "2020-02-27",
      start: "2020-02-27",
      stop: moment()
        .subtract(1, "day")
        .format("YYYY-MM-DD"),
      max: moment()
        .subtract(1, "day")
        .format("YYYY-MM-DD"),
    },
    map: {},
    entidades: {},
    lastUpdate: null,
    graphs: {
      barRace: {
        data: null,
        state: "stopped",
        description: "",
      },
      trends: {
        data: null,
        states: ["Nacional"],
        description: "",
      },
    },
  } as any,
  mutations: {
    SET_APP_STATE(state, payload?: string) {
      if (payload) {
        state.appState = payload;
      } else if (
        (state.appState =
          state.mapState === "loaded" && state.tableState === "loaded")
      ) {
        state.appState = "loaded";
      }
    },
    SET_LOCALE(state, payload?: string) {
      state.locale = payload || "es-mx";
    },
    SET_CONTENT_TEXT(state, payload: any) {
      state.text = payload;
    },
    SET_CASE_TYPE(state, payload: "positivo" | "negativo" | "finado") {
      state.caseType = payload || "positivo";
    },
    SET_COUNT_TYPE(state, payload: "numbers" | "percent") {
      state.countType = payload || "numbers";
    },
    SET_MAP_STATE(state, payload?: string) {
      state.mapState = payload;
    },
    SET_TABLE_STATE(state, payload?: string) {
      state.tableState = payload;
    },
    SET_BAR_RACE_DATA(state, payload) {
      state.graphs.barRace.data = payload;
    },
    SET_BAR_RACE_STATE(state, payload: string) {
      state.graphs.barRace.state = payload;
    },
    SET_BAR_RACE_DESC(state, payload: string) {
      state.graphs.barRace.description = payload;
    },
    SET_TREND_GRAPH_DATA(state, payload) {
      state.graphs.trends.data = payload;
    },
    ADD_TREND_GRAPH_STATE(state, payload: string) {
      if (!state.graphs.trends.states.incldues(payload)) {
        state.graphs.trends.states.push(payload);
      }
    },
    REMOVE_TREND_GRAPH_STATE(state, payload: string) {
      const idx = state.graphs.trends.states.find(payload);
      if (idx >= 0) {
        state.graphs.trends.states.splice(idx, 1);
      }
    },
    SET_TREND_GRAPH_DESC(state, payload: string) {
      state.graphs.trends.description = payload;
    },
    SET_ENTIDADES(state, payload: types.JsonData) {
      state.entidades = payload;
    },
    SET_MX_TOTALS(state, payload: types.Totals) {
      state.totals.mx = payload;
    },
    SET_STATE_TOTALS(state, payload) {
      state.totals.entidades = payload;
    },
    SET_MX_TRENDS(state, payload) {
      state.trends.mx = payload;
    },
    SET_STATE_TRENDS(state, payload) {
      state.trends.entidades = payload;
    },
    SET_DATE_RANGE_STOP(state, payload: Moment) {
      const dateStr = payload.format("YYYY-MM-DD");
      const top =
        dateStr <= state.dateRange.max ? dateStr : state.dateRange.max;
      if (top > state.dateRange.start) {
        state.dateRange.stop = top;
      } else {
        state.dateRange.stop = state.dateRange.max;
      }
    },
    SET_DATE_RANGE_START(state, payload: Moment) {
      const dateStr = payload.format("YYYY-MM-DD");
      const bottom =
        dateStr >= state.dateRange.min ? dateStr : state.dateRange.min;
      if (bottom < state.dateRange.stop) {
        state.dateRange.start = bottom;
      } else {
        state.dateRange.start = state.dateRange.min;
      }
    },
    ADD_ACTIVE_STATE(state, payload) {
      if (!state.activeStates.find((s: string) => s === payload)) {
        state.activeStates.push(payload);
        // state.activeStates.sort((a: string, b: string) =>
        //   parseInt(a) < parseInt(b) ? -1 : 1
        // );
      }
    },
    REMOVE_ACTIVE_STATE(state, payload) {
      const index = state.activeStates.findIndex((s: string) => s === payload);
      if (index >= 0) {
        state.activeStates.splice(index, 1);
      }
    },
    SET_LAST_UPDATE(state) {
      // let lastUpdated = moment(state.entidades.updatedAt)
      //   .locale("es-mx")
      //   .fromNow();
      // lastUpdated = lastUpdated.replace(
      //   lastUpdated.charAt(0),
      //   lastUpdated.charAt(0).toUpperCase()
      // );
      state.lastUpdate = moment(state.entidades.updatedAt);
    },
    SET_MAP(state, payload) {
      state.map = payload;
    },
  },
  actions: {
    intialize({ commit }) {
      const locale = "es-mx";
      const textContent: any = content.text;
      commit("SET_LOCALE", locale);
      commit("SET_CONTENT_TEXT", textContent[locale]);
    },
    fetchStatistics({ commit }) {
      const init = async (data: types.JsonData) => {
        commit("SET_ENTIDADES", data);
        commit("SET_DATE_RANGE_START", moment(this.state.dateRange.min));
        commit("SET_DATE_RANGE_STOP", moment(this.state.dateRange.max));
        const dateRange = utils.getDatesArray(
          this.state.dateRange.start,
          this.state.dateRange.stop
        );
        const mxTotals = await utils.getNatioanlTotals(data, dateRange);
        const stateTotals = await utils.getStateTotals(data, dateRange);
        const mxTrends = await utils.getNationalTrend(data, dateRange);
        const stateTrends = await utils.getStateTrends(data, dateRange);
        commit("SET_MX_TOTALS", mxTotals);
        commit("SET_STATE_TOTALS", stateTotals);
        commit("SET_MX_TRENDS", mxTrends);
        commit("SET_STATE_TRENDS", stateTrends);
        commit("SET_LAST_UPDATE");
        commit("SET_TABLE_STATE", "loaded");
        commit("SET_APP_STATE");
      };

      const dataUrl =
        "https://cors-anywhere.herokuapp.com/http://www.covidmxapi.com/mx-totals-by-location.json";
      commit("SET_APP_STATE", "loading");
      axios
        .get(dataUrl)
        .then((response) => init(response.data))
        .catch((err) => {
          commit("SET_APP_STATE", "error");
        });
    },
    fetchMap({ commit }) {
      const dataUrl =
        "https://cors-anywhere.herokuapp.com/http://www.covidmxapi.com/mexico.geojson";
      commit("SET_APP_STATE", "loading");
      axios
        .get(dataUrl)
        .then((response) => response.data)
        .then((geoJson) => {
          commit("SET_MAP", geoJson);
          commit("SET_MAP_STATE", "loaded");
          commit("SET_APP_STATE");
        })
        .catch((err) => {
          commit("SET_APP_STATE", "error");
        });
    },
  },
  modules: {},
});
