<template>
  <v-app
    id="app"
    :style="{ background: $vuetify.theme.themes[theme].background }"
  >
    <div fluid>
      <v-app-bar color="main" fixed dark>
        <div v-show="$screen.breakpoint === 'xs'">
          <v-app-bar-nav-icon @click="drawer = true" />
        </div>
        <div>
          <v-toolbar-title
            >{{ this.text.appTitle }}
            {{ $screen.breakpoint === "xs" ? "" : " 🇲🇽" }}</v-toolbar-title
          >
        </div>
        <!-- START: if '!xs' -->
        <v-tabs
          v-if="$screen.breakpoint !== 'xs' && this.text.menu"
          background-color="main"
          dark
          :class="
            $vuetify.theme.isDark ? 'light-shade--text' : 'light-shade--text'
          "
          centered
          grow
          icons-and-text
        >
          <v-tab to="/"
            >{{ this.text.menu.info }}<v-icon>fas fa-virus</v-icon>
          </v-tab>
          <v-tab to="/cifras"
            >{{ this.text.menu.table }}<v-icon>fas fa-chart-pie</v-icon>
          </v-tab>
          <v-tab to="/mapa"
            >{{ this.text.menu.map }}<v-icon>fas fa-globe-americas</v-icon>
          </v-tab>
          <v-tab to="/historial"
            >{{ this.text.menu.trend }}<v-icon>fas fa-chart-line</v-icon>
          </v-tab>
          <v-tab to="/graficas"
            >{{ this.text.menu.bars }}<v-icon>fas fa-chart-bar</v-icon>
          </v-tab>
        </v-tabs>

        <!-- END: if '!xs'-->
      </v-app-bar>
      <!-- START: if 'xs' -->
      <div v-if="$screen.breakpoint === 'xs' && this.text.menu">
        <v-navigation-drawer
          v-model="drawer"
          fixed
          temporary
          :class="$vuetify.theme.isDark ? 'white--text' : 'text--text'"
        >
          <v-list nav shaped fluid>
            <v-list-item-group active-class="menu--text text--accent-4">
              <v-list-item to="/">
                <v-list-item-icon>
                  <v-icon>fas fa-virus</v-icon>
                </v-list-item-icon>
                <v-list-item-title>{{ this.text.menu.info }}</v-list-item-title>
              </v-list-item>
              <v-list-item to="/cifras">
                <v-list-item-icon>
                  <v-icon>fas fa-chart-pie</v-icon>
                </v-list-item-icon>
                <v-list-item-title>{{
                  this.text.menu.table
                }}</v-list-item-title>
              </v-list-item>
              <v-list-item to="/mapa">
                <v-list-item-icon>
                  <v-icon>fas fa-globe-americas</v-icon>
                </v-list-item-icon>
                <v-list-item-title>{{ this.text.menu.map }}</v-list-item-title>
              </v-list-item>
              <v-list-item to="/historial">
                <v-list-item-icon>
                  <v-icon>fas fa-chart-line</v-icon>
                </v-list-item-icon>
                <v-list-item-title>{{
                  this.text.menu.trend
                }}</v-list-item-title>
              </v-list-item>
              <v-list-item to="/graficas">
                <v-list-item-icon>
                  <v-icon>fas fa-chart-bar</v-icon>
                </v-list-item-icon>
                <v-list-item-title>{{ this.text.menu.bars }}</v-list-item-title>
              </v-list-item>
            </v-list-item-group>
          </v-list>

          <v-switch
            v-model="$vuetify.theme.dark"
            class="text--text ml-5"
            label="Modo oscuro"
          ></v-switch>
        </v-navigation-drawer>
      </div>
      <!-- END: if 'xs' -->
    </div>
    <div class="mt-5">
      <v-main class="mt-5" fluid>
        <v-container class="fill-height" block>
          <v-row align="center" justify="center">
            <v-col class="text-center">
              <router-view />
            </v-col>
          </v-row>
        </v-container>
      </v-main>
    </div>
    <v-footer color="main" app max-height="3.5rem">
      <span class="white--text">{{ lastUpdated }} </span>
      <v-spacer></v-spacer>
      <v-switch
        v-if="$screen.breakpoint !== 'xs'"
        dense
        v-model="$vuetify.theme.dark"
        class="text-caption text--text "
        label="Modo oscuro"
      ></v-switch>
    </v-footer>
  </v-app>
</template>

<script>
import { mapState } from "vuex";

export default {
  props: {
    source: String,
  },
  data: () => ({
    drawer: null,
  }),
  computed: {
    ...mapState(["locale", "text", "lastUpdate"]),
    lastUpdated() {
      if (this.lastUpdate) {
        const date = this.lastUpdate.locale(this.locale);
        let day = date.format("dddd");
        day = day.replace(day.charAt(0), day.charAt(0).toUpperCase());
        let month = date.format("MMMM");
        month = month.replace(month.charAt(0), month.charAt(0).toUpperCase());
        if (this.locale === "en-us") {
          return `Updated: ${day}, ${month} ${date.format("DD")}, ${date.format(
            "YYYY"
          )}`;
        } else {
          return `Actualizado: ${day}, ${date.format(
            "DD"
          )} de ${month}, ${date.format("YYYY")}`;
        }
      } else {
        return "";
      }
    },
    theme() {
      return this.$vuetify.theme.dark ? "dark" : "light";
    },
  },
  mounted() {
    this.$store.dispatch("intialize");
    this.$store.dispatch("fetchStatistics");
    this.$store.dispatch("fetchMap");
  },
};
</script>

<style lang="scss">
.footer-text {
  text-transform: capitalize;
}
</style>
