<template>
  <div>
    <selection-buttons />
    <v-container fluid>
      <v-row :v-if="mapReady">
        <v-col cols="12">
          <v-sheet rounded color="map-background" class="elevation-2" dark>
            <canvas
              class="pt-5"
              id="mapCanvas"
              @click.prevent="handleClick"
            ></canvas>
          </v-sheet>
        </v-col>
      </v-row>
      <v-row>
        <v-col cols="12">
          <v-sheet
            v-if="selectedStates"
            class="elevation-3"
            :class="$vuetify.theme.isDark ? 'light-shade--text' : 'text--text'"
            rounded
          >
            <v-row block
              ><span class="mt-2 text-h4 title">{{
                this.getLabels().mapTitle
              }}</span></v-row
            >
            <v-list align="start">
              <v-list-item-group>
                <v-list-item v-for="(state, i) in listData" :key="i">
                  <v-list-item-content>
                    <v-list-item-title v-text="state.title"></v-list-item-title>
                    <v-list-item-subtitle
                      v-text="state.subtitle.label"
                    ></v-list-item-subtitle>
                  </v-list-item-content>
                  <v-list-item-action>
                    <v-btn @click.prevent="removeActiveState(state.id)" icon>
                      <v-icon color="grey lighten-1">fas fa-minus</v-icon>
                    </v-btn>
                  </v-list-item-action>
                </v-list-item>
              </v-list-item-group>
            </v-list>
          </v-sheet>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>
import { mapState } from "vuex";
import SelectionButtons from "@/components/SelectionButtons";
import numeral from "numeral";
import * as ChartGeo from "chartjs-chart-geo";
import Chart from "chart.js";

export default {
  name: "map-viewer",
  components: { SelectionButtons },
  data() {
    return {
      mapChart: null,
      componentKey: 0,
    };
  },
  computed: {
    ...mapState([
      "appState",
      "map",
      "entidades",
      "totals",
      "lastUpdate",
      "activeStates",
      "caseType",
      "countType",
      "text",
    ]),
    mapReady() {
      return this.appState === "loaded" && this.map?.features?.length > 0;
    },
    maxCases() {
      if (this.caseType === "finado") {
        return this.totals.mx.finado;
      }
      return this.totals.mx.positivo;
    },
    casosPositivos() {
      const positivos = {};
      if (this.entidades) {
        for (let i = 1; i <= 32; i++) {
          const id = `${i}`;
          const entidad = this.entidades[id];
          if (!entidad) {
            break;
          }
          const totalPositive = entidad.positive.reduce((total, day) => {
            return (total += day.cases);
          }, 0);
          positivos[id] = totalPositive;
        }
      }
      return positivos;
    },
    selectedStates() {
      return this.activeStates.length > 0;
    },
    geoLabels() {
      const labels = {
        mapTitle: this.text.labels.mapTitle,
      };
      for (let i = 1; i <= 32; i++) {
        labels[`${i}`] = this.entidades[`${i}`].name;
      }
      return labels;
    },
    listData() {
      let listData = [];
      if (this.activeStates?.length > 0) {
        listData = this.activeStates.map((id) => {
          const name = this.geoLabels[id];
          const value = this.getValue(name);

          return { id, title: name, subtitle: value };
        });
      }
      return listData;
    },
  },
  beforeUpdate() {
    this.drawMap();
  },
  mounted() {
    this.$store.commit("SET_BAR_RACE_STATE", "stopped");
    this.drawMap();
  },
  methods: {
    drawMap() {
      if (this.mapReady) {
        const mapa = this.map.features;
        this.mapChart = new Chart(
          document.getElementById("mapCanvas").getContext("2d"),
          {
            type: "choropleth",
            data: {
              labels: mapa.map((d) => {
                const entityId = d.properties.entityId;
                const name = this.entidades[entityId].name;
                return name;
              }),
              datasets: [
                {
                  outline: this.map.features,
                  clipMap: "outline",
                  data: mapa.map((d) => {
                    const entityId = d.properties.entityId;
                    const name = this.entidades[entityId].name;
                    const { value, label } = this.getValue(name);

                    return { feature: d, value, label };
                  }),
                },
              ],
            },
            options: {
              legend: {
                display: false,
              },
              scale: {
                projection: "mercator",
              },
              geo: {
                colorScale: {
                  display: true,
                  property: "value",
                  position: "bottom",
                  quantize: 100,
                  interpolate: "interpolateYlGnBu",
                  legend: {
                    position: "bottom-left",
                  },
                },
              },
            },
          }
        );
      }
    },
    getLabels() {
      const labels = {
        mapTitle: this.text?.labels?.mapTitle || "Casos de COVID-19 en México",
        num: this.text?.labels?.num || "Cifras",
        per: this.text?.labels?.num || "Porcentaje",
        estado: this.text?.labels?.estado || "Estado",
        pos: this.text?.table?.national?.headers?.pos || "Positivo",
        neg: this.text?.table?.national?.headers?.neg || "Negativo",
        fin: this.text?.table?.national?.headers?.fin || "Finado",
      };
      if (this.isXs) {
        labels.num = "";
        labels.per = "";
        labels.pos = "";
        labels.fin = "";
      }
      return labels;
    },
    getValue(name) {
      let value = 0;
      if (this.countType === "percent") {
        const { positivo, negativo } = this.totals.entidades[name];
        const total = positivo + negativo;

        value = (this.totals.entidades[name][this.caseType] / total) * 100;
        value = parseFloat(value.toFixed(1));
      } else {
        value = this.totals.entidades[name][this.caseType];
      }
      let label = value.toString();
      if (this.countType === "percent") {
        label = numeral(value / 100).format("0.1%");
      } else {
        label = numeral(value).format("0,0");
      }

      return { value, label };
    },
    removeActiveState(id) {
      this.$store.commit("REMOVE_ACTIVE_STATE", id);
    },
    getPositive(id) {
      let result = "";
      if (id === "0") {
        result = numeral(this.totals.mx.positivo).format("0,0");
      } else {
        const entidad = this.entidades[id].name;
        result = numeral(this.totals.entidades[entidad].positivo).format("0,0");
      }
      return result;
    },
    getDeaths(id) {
      let result = "";
      if (id === "0") {
        result = numeral(this.totals.mx.finado).format("0,0");
      } else {
        const entidad = this.entidades[id].name;
        result = numeral(this.totals.entidades[entidad].finado).format("0,0");
      }
      return result;
    },
    async handleClick(e) {
      const activePoints = this.mapChart.getElementsAtEvent(e);
      const firstPoint = activePoints[0];
      const entityId = firstPoint?.feature?.properties?.entityId;
      if (entityId) {
        if (this.activeStates.find((s) => s === entityId)) {
          this.$store.commit("REMOVE_ACTIVE_STATE", entityId);
        } else if (this.activeStates.length < 4) {
          this.$store.commit("ADD_ACTIVE_STATE", entityId);
        } else {
          await this.$store.commit("REMOVE_ACTIVE_STATE", this.activeStates[0]);
          this.$store.commit("ADD_ACTIVE_STATE", entityId);
        }
      }

      return "";
    },
    forceRerender() {
      this.componentKey++;
    },
  },
  watch: {
    caseType() {
      this.drawMap();
    },
    countType() {
      this.drawMap();
    },
    "$screen.width"() {
      this.forceRerender();
    },
  },
};
</script>

<style lang="scss" scoped>
.title {
  width: 100%;
}
</style>
