<template>
  <v-container fluid>
    <v-row align="start">
      <v-spacer />
      <v-col v-show="!hideCases" cols="3">
        <v-btn
          x-large
          :rounded="!isXs"
          :block="!isXs"
          :fab="isXs"
          :elevation="caseType === 'positivo' ? '0' : '5'"
          :depressed="caseType === 'positivo'"
          :class="
            caseType === 'positivo' ? 'pressed-button--text' : 'text--text'
          "
          :color="caseType === 'positivo' ? 'warning' : 'secondary'"
          @click.prevent="setCaseType('positivo')"
        >
          <v-icon :class="!isSm ? 'mr-5' : ''">fas fa-head-side-virus</v-icon>
          {{ this.getLabels().pos }}
        </v-btn>
      </v-col>
      <v-col v-show="!hideCases" cols="3">
        <v-btn
          x-large
          :rounded="!isXs"
          :block="!isXs"
          :fab="isXs"
          :elevation="caseType === 'finado' ? '0' : '5'"
          :depressed="caseType === 'finado'"
          :class="caseType === 'finado' ? 'pressed-button--text' : 'text--text'"
          :color="caseType === 'finado' ? 'red' : 'secondary'"
          @click.prevent="setCaseType('finado')"
          ><v-icon :class="!isSm ? 'mr-5' : ''">fas fa-skull</v-icon>
          {{ this.getLabels().fin }}</v-btn
        >
      </v-col>
      <v-col v-show="!hideCounts" cols="3">
        <v-btn
          x-large
          :rounded="!isXs"
          :block="!isXs"
          :fab="isXs"
          :elevation="countType === 'numbers' ? '0' : '5'"
          :depressed="countType === 'numbers'"
          :class="
            countType === 'numbers' ? 'pressed-button--text' : 'text--text'
          "
          :color="countType === 'numbers' ? 'main' : 'secondary'"
          @click.prevent="setCountType('numbers')"
          ><v-icon :class="!isSm ? 'mr-5' : ''">fas fa-users</v-icon>
          {{ this.getLabels().num }}</v-btn
        >
      </v-col>
      <v-col v-show="!hideCounts" cols="3">
        <v-btn
          x-large
          :rounded="!isXs"
          :block="!isXs"
          :fab="isXs"
          :elevation="countType === 'percent' ? '0' : '5'"
          :depressed="countType === 'percent'"
          :class="
            countType === 'percent' ? 'pressed-button--text' : 'text--text'
          "
          :color="countType === 'percent' ? 'main' : 'secondary'"
          @click.prevent="setCountType('percent')"
          ><v-icon :class="!isSm ? 'mr-5' : ''">fas fa-percentage</v-icon>
          {{ this.getLabels().per }}</v-btn
        >
      </v-col>
      <v-spacer />
    </v-row>
  </v-container>
</template>

<script>
import { mapState } from "vuex";
export default {
  name: "selection-buttons",
  data() {
    return {};
  },
  props: ["hideCases", "hideCounts"],
  computed: {
    ...mapState(["appState", "text", "caseType", "countType"]),
    isSm() {
      return ["sm", "xs"].includes(this.$screen.breakpoint);
    },
    isXs() {
      return this.$screen.breakpoint === "xs";
    },
  },
  methods: {
    getLabels() {
      const labels = {
        title: this.text?.labels?.mapTitle || "Casos de COVID-19 en México",
        num: this.text?.labels?.num || "Cifras",
        per: this.text?.labels?.per || "Porcentaje",
        estado: this.text?.labels?.estado || "Estado",
        pos: this.text?.table?.national?.headers?.pos || "Positivo",
        neg: this.text?.table?.national?.headers?.neg || "Negativo",
        fin: this.text?.table?.national?.headers?.fin || "Finado",
      };
      if (this.isSm) {
        labels.num = "";
        labels.per = "";
        labels.pos = "";
        labels.fin = "";
      }
      return labels;
    },
    setCaseType(value) {
      this.$store.commit("SET_CASE_TYPE", value);
    },
    setCountType(value) {
      this.$store.commit("SET_COUNT_TYPE", value);
    },
  },
};
</script>
<style lang="scss" scoped></style>
