<template>
  <div>
    <selection-buttons :hideCases="true" />
    <v-container fluid>
      <v-row>
        <v-col cols="12">
          <v-sheet
            class="elevation-3"
            :class="$vuetify.theme.isDark ? 'light-shade--text' : 'text--text'"
            rounded
          >
            <v-row block
              ><span class="mt-2 text-h4 title">{{
                this.getLabels().title
              }}</span></v-row
            >
            <v-simple-table block>
              <thead>
                <tr>
                  <th class="text-center">{{ this.getLabels().pos }}</th>
                  <th class="text-center">{{ this.getLabels().neg }}</th>
                  <th class="text-center">{{ this.getLabels().fin }}</th>
                </tr>
              </thead>
              <tbody>
                <tr>
                  <td>{{ this.mxTotal.positivo }}</td>
                  <td>{{ this.mxTotal.negativo }}</td>
                  <td>{{ this.mxTotal.finado }}</td>
                </tr>
              </tbody>
            </v-simple-table>
            <v-text-field
              v-model="search"
              append-icon="fas fa-search"
              class="mx-5"
              :label="this.getLabels().search"
              single-line
              hide-details
            ></v-text-field>
            <v-data-table
              dense
              :headers="headers"
              :items="stateItems"
              :items-per-page="32"
              class="elevation-1"
              :custom-sort="customSort"
              :search="search"
              :custom-filter="insensitiveSearch"
              must-sort
              hide-default-footer=""
            ></v-data-table>
          </v-sheet>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>
import SelectionButtons from "@/components/SelectionButtons";
import { mapState } from "vuex";
import numeral from "numeral";

export default {
  name: "table-viewer",
  components: { SelectionButtons },
  data() {
    return {
      search: "",
      componentKey: 0,
    };
  },
  computed: {
    ...mapState([
      "appState",
      "totals",
      "entidades",
      "lastUpdate",
      "locale",
      "text",
      "caseType",
      "countType",
    ]),
    headers() {
      const headers = [];
      headers.push({
        text: this.getLabels().estado,
        align: "start",
        sortable: true,
        value: "name",
      });
      headers.push({
        text: this.getLabels().pos,
        align: "end",
        sortable: true,
        sortBy: this.countType === "percent" ? "positivoPrc" : "positivo",
        value: this.countType === "percent" ? "positivoPrcStr" : "positivoStr",
        filterable: false,
      });
      headers.push({
        text: this.getLabels().neg,
        align: "end",
        sortable: true,
        sortBy: this.countType === "percent" ? "negativoPrc" : "negativo",
        value: this.countType === "percent" ? "negativoPrcStr" : "negativoStr",
        filterable: false,
      });
      headers.push({
        text: this.getLabels().fin,
        align: "end",
        sortable: true,
        sortBy: this.countType === "percent" ? "finadoPrc" : "finado",
        value: this.countType === "percent" ? "finadoPrcStr" : "finadoStr",
        filterable: false,
      });

      return headers;
    },
    mxTotal() {
      const result = {
        positive: 0,
        negative: 0,
        deaths: 0,
      };
      if (this.totals.mx) {
        const { positivo, negativo, finado } = this.totals.mx;
        const pruebasTotales = positivo + negativo;
        const denominador = pruebasTotales > 0 ? pruebasTotales : 1;

        const mxTotal = {
          id: `MEX`,
          name: `Estados Unidos Mexicanos`,
          positivo: positivo,
          negativo: negativo,
          finado: finado,
          positivoPrc: positivo / denominador,
          negativoPrc: negativo / denominador,
          finadoPrc: finado / denominador,
          positivoStr: numeral(positivo).format("0,0"),
          negativoStr: numeral(negativo).format("0,0"),
          finadoStr: numeral(finado).format("0,0"),
          positivoPrcStr: numeral(positivo / denominador).format("0.1%"),
          negativoPrcStr: numeral(negativo / denominador).format("0.1%"),
          finadoPrcStr: numeral(finado / denominador).format("0.1%"),
        };
        if (this.countType === "percent") {
          result.positivo = mxTotal.positivoPrcStr;
          result.negativo = mxTotal.negativoPrcStr;
          result.finado = mxTotal.finadoPrcStr;
        } else {
          result.positivo = mxTotal.positivoStr;
          result.negativo = mxTotal.negativoStr;
          result.finado = mxTotal.finadoStr;
        }
      }
      return result;
    },
    stateItems() {
      const stateItems = [];
      for (const name in this.totals.entidades) {
        let code;
        for (let i = 1; i <= 32; i++) {
          if (this.entidades[`${i}`].name == name) {
            code = this.entidades[`${i}`].code;
            break;
          }
        }
        const { positivo, negativo, finado } = this.totals.entidades[name];
        const pruebasTotales = positivo + negativo;
        const denominador = pruebasTotales > 0 ? pruebasTotales : 1;

        const stateTotal = {
          id: code,
          name,
          positivo: positivo,
          negativo: negativo,
          finado: finado,
          positivoPrc: positivo / denominador,
          negativoPrc: negativo / denominador,
          finadoPrc: finado / denominador,
          positivoStr: numeral(positivo).format("0,0"),
          negativoStr: numeral(negativo).format("0,0"),
          finadoStr: numeral(finado).format("0,0"),
          positivoPrcStr: numeral(positivo / denominador).format("0.1%"),
          negativoPrcStr: numeral(negativo / denominador).format("0.1%"),
          finadoPrcStr: numeral(finado / denominador).format("0.1%"),
        };

        stateItems.push(stateTotal);
      }
      this.forceRerender();
      return stateItems;
    },
  },
  methods: {
    getLabels() {
      const labels = {
        title: this.text?.labels?.natTotal || "Total Nacional",
        num: this.text?.labels?.num || "Cifras",
        per: this.text?.labels?.num || "Porcentaje",
        estado: this.text?.labels?.estado || "Estado",
        pos: this.text?.table?.national?.headers?.pos || "Positivo",
        neg: this.text?.table?.national?.headers?.neg || "Negativo",
        fin: this.text?.table?.national?.headers?.fin || "Finado",
        search: this.text?.labels?.search || "Búsqueda por estado",
      };
      return labels;
    },
    customSort(items, index, isDesc) {
      if (!items || !index || index.length < 1) {
        return items || [];
      }
      const sortBy = index[0];
      const descending = isDesc[0];

      if (descending) {
        items.sort((a, b) => {
          if (sortBy === "name") {
            return a.name > b.name ? -1 : 1;
          } else if (sortBy === "positivoStr") {
            return a.positivo > b.positivo ? -1 : 1;
          } else if (sortBy === "negativoStr") {
            return a.negativo > b.negativo ? -1 : 1;
          } else if (sortBy === "finadoStr") {
            return a.finado > b.finado ? -1 : 1;
          } else if (sortBy === "positivoPrcStr") {
            return a.positivoPrc > b.positivoPrc ? -1 : 1;
          } else if (sortBy === "negativoPrcStr") {
            return a.negativoPrc > b.negativoPrc ? -1 : 1;
          } else if (sortBy === "finadoPrcStr") {
            return a.finadoPrc > b.finadoPrc ? -1 : 1;
          }
        });
      } else {
        items.sort((a, b) => {
          if (sortBy === "name") {
            return a.name < b.name ? -1 : 1;
          } else if (sortBy === "positivoStr") {
            return a.positivo < b.positivo ? -1 : 1;
          } else if (sortBy === "negativoStr") {
            return a.negativo < b.negativo ? -1 : 1;
          } else if (sortBy === "finadoStr") {
            return a.finado < b.finado ? -1 : 1;
          } else if (sortBy === "positivoPrcStr") {
            return a.positivoPrc < b.positivoPrc ? -1 : 1;
          } else if (sortBy === "negativoPrcStr") {
            return a.negativoPrc < b.negativoPrc ? -1 : 1;
          } else if (sortBy === "finadoPrcStr") {
            return a.finadoPrc < b.finadoPrc ? -1 : 1;
          }
        });
      }

      return items;
    },
    insensitiveSearch(name, search, item) {
      const normalizedName = name
        .normalize("NFD")
        .replace(/[\u0300-\u036f]/g, "")
        .toLowerCase();
      const normalizedSearch = search
        .normalize("NFD")
        .replace(/[\u0300-\u036f]/g, "")
        .toLowerCase();
      return normalizedName.includes(normalizedSearch);
    },
    forceRerender() {
      this.componentKey++;
    },
  },
  mounted() {
    this.$store.commit("SET_BAR_RACE_STATE", "stopped");
  },
};
</script>

<style lang="scss" scoped>
.title {
  width: 100%;
}
</style>
