<template>
  <div>
    EmptyState
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  name: "empty-state",
  porps: ["forceEmpty", "forceIcon", "forceLabel", "forceDescription"],
  data() {
    return {
      forceEmpty: false,
    };
  },
  computed: {
    ...mapState(["appState"]),
    labels() {
      let icon = "warning";
      let label = "Sin Datos";
      let description = "No hay información para mostrar";

      if (this.appState === "loading") {
        icon = "hourglass_full";
        label = "Actualizando";
        description = "Obteniendo datos actualizados...";
      }
      return { icon, label, description };
    },
  },
};
</script>
<style lang="scss" scoped>
.md-card {
  margin-top: 1rem;
  padding-top: 1rem;
  padding-bottom: 1rem;
}
</style>
