import Vue from "vue";
import VueRouter, { RouteConfig } from "vue-router";
import Information from "../views/InformationViewer.vue";
import TableViewer from "../views/TableViewer.vue";
import MapViewer from "../views/MapViewer.vue";
import TrendViewer from "../views/TrendViewer.vue";
import BarRaceViewer from "../views/BarRaceViewer.vue";

Vue.use(VueRouter);

const routes: Array<RouteConfig> = [
  {
    path: "/",
    name: "Information",
    component: Information,
  },
  {
    path: "/mapa",
    name: "MapViewer",
    component: MapViewer,
  },
  {
    path: "/cifras",
    name: "TableViewer",
    component: TableViewer,
  },
  {
    path: "/graficas",
    name: "BarRaceViewer",
    component: BarRaceViewer,
  },
  {
    path: "/historial",
    name: "TrendViewer",
    component: TrendViewer,
  },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

export default router;
