<template>
  <div>
    <selection-buttons />
    <v-container fluid>
      <v-row :v-if="appState === 'loaded'">
        <v-col cols="12" align="start">
          <v-menu
            offset-y
            offset-x
            slide-y
            rounded
            :close-on-content-click="isXs"
            :max-height="$screen.height / 1.6"
          >
            <template v-slot:activator="{ on, attrs }">
              <v-btn
                rounded
                x-large
                class="px-5"
                :color="$vuetify.theme.isDark ? 'dark-shade' : 'secondary'"
                :class="
                  $vuetify.theme.isDark ? 'light-shade--text' : 'text--text'
                "
                v-bind="attrs"
                v-on="on"
              >
                {{ labels.statesButton }}
              </v-btn>
            </template>
            <v-list dense>
              <v-list-item
                v-for="name in entidades"
                :key="name"
                class="menu-checkbox"
              >
                <v-list-item-title>
                  <v-checkbox
                    v-model="seleccionado"
                    :value="name"
                    :label="name"
                    :color="colors[name]"
                  ></v-checkbox>
                </v-list-item-title>
              </v-list-item>
            </v-list>
          </v-menu>
        </v-col>
      </v-row>
      <v-row :v-if="appState === 'loaded'">
        <v-col cols="12">
          <v-sheet
            id="lineChartGroup"
            rounded
            color="chart-background"
            class="elevation-2"
            dark
          >
            <multi-line-chart
              :chartData="chartData"
              :options="chartOptions"
              :height="this.$screen.height / 1.8"
              :width="this.$screen.width / 1.8"
              :key="componentKey"
            />
          </v-sheet>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>
import moment, { max } from "moment";
import { mapState } from "vuex";
import SelectionButtons from "@/components/SelectionButtons.vue";
import MultiLineChart from "@/components/MultiLineChart.vue";
import randomColor from "randomcolor";
import { color } from "d3";

moment.locale("es-MX");

export default {
  name: "trend-viewer",
  components: {
    SelectionButtons,
    MultiLineChart,
  },
  data() {
    return {
      axisType: "linear",
      seleccionado: ["Nacional"],
      colors: {},
      componentKey: 0,
      chartOptions: {
        responsive: true,
        maintainAspectRatio: false,
        scales: {
          animation: { animateScale: true, duration: 3000 },
          legend: {
            position: "right",
            align: "center",
          },
          tooltips: {
            bodyFontSize: 24,
          },
          yAxes: [
            {
              type: this.axisType,
              afterBuildTicks: (chartObj) => {
                if (chartObj.end === 1) {
                  chartObj.ticks = [];
                  return;
                }
                if (this.countType === "percent") {
                  const delta = chartObj.ticks[0] / 20;
                  let base = 0;
                  chartObj.ticks = [];

                  for (let i = 0; i < 20; i++) {
                    base += delta;
                    chartObj.ticks.push(base);
                  }
                } else {
                  const delta = chartObj.ticks[0] / 10;
                  if (delta === 1) {
                    return [1];
                  }
                  let base = 0;
                  chartObj.ticks = [];

                  for (let i = 0; i < 10; i++) {
                    base += delta;
                    chartObj.ticks.push(base);
                  }
                }
                // return;
                // //Build ticks labelling as per your need
                // //for logarithmic
                // chartObj.ticks = [];
                // chartObj.ticks.push(0.1);
                // chartObj.ticks.push(1);
                // chartObj.ticks.push(10);
                // chartObj.ticks.push(100);
                // chartObj.ticks.push(1000);
              },
              stacked: false,
              ticks: {
                min: 0,
                stepSize: 200,
                callback: function(value, index, values) {
                  return Number(value.toString()); //pass tick values as a string into Number function
                },
              },
              scaleLabel: {
                display: true,
                labelString: "",
              },
            },
          ],
          xAxes: [
            {
              type: "time",
              distribution: "series",
              gridLines: {
                display: true,
              },
            },
          ],
        },
        tooltips: {
          mode: "interpolate",
          intersect: false,
        },
        plugins: {
          datalabels: {
            backgroundColor: "#eee",
            anchor: "end",
            display: "auto",
            align: "left",
            clip: true,
            offset: 20,
            font: {
              size: 18,
            },
          },
        },
      },
    };
  },
  mounted() {
    const colors = {
      Nacional: randomColor({ hue: "red", luminosity: "dark" }),
    };

    this.colors = colors;
  },
  beforeUpdate() {
    if (this.seleccionado.length > 5) {
      this.seleccionado.splice(4, 1);
    }
    const hues = {
      "1": "blue",
      "2": "orange",
      "3": "purple",
      "4": "green",
    };
    let counter = 4;
    for (const name in this.totals.entidades) {
      if (!this.colors[name]) {
        this.colors[name] = randomColor({
          hue: hues[`${(counter % 4) + 1}`],
          luminosity: "dark",
        });
        counter++;
      }
    }

    this.forceRerender();
  },
  computed: {
    ...mapState([
      "appState",
      "dateRange",
      "trends",
      "graphs",
      "totals",
      "caseType",
      "countType",
    ]),
    isXs() {
      return this.$screen.breakpoint === "xs";
    },
    maxWidth() {
      // let maxWidth = this.$screen.width;
      // if (this.$screen.breakpoint === "xl") {
      //   maxWidth = this.$screen.width / 1.8;
      // }
      // return maxWidth;
      return this.$screen.width / 1.5;
    },
    maxHeight() {
      // let maxHeight = this.$screen.height;
      // if (this.$screen.breakpoint === "xl") {
      //   maxHeight = this.$screen.height / 1.4;
      // } else {
      //   maxHeight = this.$screen.height / 1.4;
      // }
      // return maxHeight;
      return this.$screen.height / 1.4;
    },
    chartData() {
      const chartData = {};
      const labels = [];
      const dataSets = [];
      if (this.trends.mx && this.trends.entidades) {
        for (const entidad in this.entidades) {
          const name = this.entidades[entidad];
          if (this.seleccionado.includes(name)) {
            const startDate = moment(this.dateRange.start);
            const stopDate = moment(this.dateRange.stop);
            const dayDiff = stopDate.diff(startDate, "days");
            const dataValues = [];
            for (let i = 0; i <= dayDiff; i++) {
              const date = moment(this.dateRange.start).add(i, "days");
              labels.push(date);
              let cases = 0;
              let positivo = 0;
              let negativo = 0;
              if (name === "Nacional") {
                positivo = this.totals.mx.positivo;
                negativo = this.totals.mx.negativo;
                cases = this.trends.mx[this.caseType][i];
              } else {
                positivo = this.totals.entidades[name].positivo;
                negativo = this.totals.entidades[name].negativo;
                cases = this.trends.entidades[name][this.caseType][i];
              }

              if (this.countType === "percent") {
                const pruebasTotales = positivo + negativo;
                const denominador = pruebasTotales > 0 ? pruebasTotales : 1;
                const percent = cases / denominador;
                cases = parseFloat((percent * 100).toFixed(1));
              }
              dataValues.push(cases);
            }

            let color = randomColor({ luminosity: "dark" });
            if (name === "Nacional") {
              color = this.colors.Nacional;
            } else {
              color = this.colors[name];
            }

            const entityData = {
              label: name,
              data: dataValues,
              borderColor: color,
              borderWidth: 3,
              fill: false,
              borderAlign: "inner",
              hoverBorderWidth: 0,
              pointBorderWidth: 0,
              pointStyle: "cross",
              datalabels: {
                color: this.colors[name],
              },
            };
            dataSets.push(entityData);
          }
        }
      }

      chartData.labels = labels;
      chartData.datasets = dataSets;
      return chartData;
    },
    entidades() {
      const nombres = [];
      for (const name in this.trends.entidades) {
        nombres.push(name);
      }
      const sorted = nombres.sort();
      return ["Nacional", ...sorted];
    },
    additionalStates() {
      let additionalStates = [];
      if (this.activeStates?.length > 0) {
        additionalStates = this.activeStates.map((id) => {
          const name = this.entidades[id].name;
          additionalStates.push(name);
        });
      }
      return additionalStates;
    },
    labels() {
      const labels = {
        statesButton: "Seleccionar Entidades Federativas",
      };
      if (this.$screen.breakpoint === "xs") {
        labels.statesButton = "Entidades Federativas";
      }
      return labels;
    },
  },
  methods: {
    setAxisType(val) {
      this.axisType = val || "linear";
      this.forceRerender();
    },
    forceRerender() {
      this.componentKey++;
    },
  },
};
</script>

<style lang="scss" scoped>
.top-button {
  margin-bottom: 10px;
}
.menu-checkbox {
  width: 100%;
}

.text-icon {
  margin-top: -0.5rem;
}
</style>
