<template>
  <div>
    <v-card v-if="data" class="bar-chart-race-card">
      <p class="title footer-text">
        {{ graphs.barRace.description }}
      </p>
      <div id="barRaceDiv" :height="height" :width="width"></div>
    </v-card>
  </div>
</template>

<script>
import { mapState } from "vuex";
import * as d3 from "d3";
import { select as d3Select } from "d3";
import * as topojson from "topojson-client";
import { createBarChartRace } from "@/graphHelpers/barRace.js";

export default {
  name: "bar-chart-race",
  props: [
    "appState",
    "barChartName",
    "data",
    "height",
    "width",
    "viewerState",
    "valueType",
  ],
  data() {
    return {
      animation: null,
    };
  },
  computed: {
    ...mapState(["entidades", "graphs"]),
    graphWidth() {
      let graphWidth = this.$screen.width / 1.3;
      if (this.$screen.breakpoint === "xs") {
        graphWidth = this.$screen.width / 1.1;
      } else if (this.$screen.breakpoint === "sm") {
        graphWidth = this.$screen.width / 1.08;
      } else if (this.$screen.breakpoint === "md") {
        graphWidth = this.$screen.width / 1.07;
      } else if (this.$screen.breakpoint === "lg") {
        graphWidth = this.$screen.width / 1.25;
      } else if (this.$screen.breakpoint === "xl") {
        graphWidth = this.$screen.width / 1.5;
      }
      return graphWidth;
    },
  },
  mounted() {
    if (this.$screen && this.entidades && this.viewerState === "running") {
      this.mountBarChartRace(this.data);
    }
  },
  methods: {
    async mountBarChartRace(data) {
      const entityNames = [];
      for (let i = 1; i <= 32; i++) {
        entityNames.push(this.entidades[`${i}`].name);
      }
      const h = this.height;
      let topN = 16;
      if (h < 450) {
        topN = 4;
      } else if (h < 700) {
        topN = 6;
      } else if (h < 800) {
        topN = 10;
      } else if (h < 1000) {
        topN = 12;
      } else if (h < 1200) {
        topN = 14;
      } else if (h < 1400) {
        topN = 16;
      } else if (h < 1600) {
        topN = 18;
      }
      const options = {
        topN,
        durationSec: 10,
        timeIndex: "date",
        columnNames: entityNames,
        width: this.graphWidth,
        height: this.height / 1.37,
        valueType: this.valueType,
        stopFunc: (val) => {
          if (["running", "stopped"].includes(this.graphs.barRace.state)) {
            this.$store.commit("SET_BAR_RACE_STATE", val);
          }
        },
      };
      this.animation = createBarChartRace("barRaceDiv", data, options);
    },
    stopAnimation() {
      if (this.animation && this.animation.stop) {
        this.animation.stop();
      }
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.footer-text {
  color: "text--text";
}
</style>
