<template>
  <v-container v-if="text && text.infoCards" fluid>
    <v-layout wrap align-center>
      <v-row>
        <v-col cols="12">
          <v-card>
            <v-card-title>
              <v-spacer />
              <v-icon class="mx-5 warning--text">fas fa-head-side-virus</v-icon>
              <span class="warning--text">{{ this.getTotals.positivo }}</span>
              <v-spacer />
              <v-icon class="mx-5 red--text">fas fa-skull</v-icon>
              <span class="red--text">{{ this.getTotals.finado }}</span>
              <v-spacer />
            </v-card-title>
          </v-card>
        </v-col>
      </v-row>
      <v-row>
        <v-col
          v-for="card in text.infoCards"
          :key="card.id"
          xs12
          sm6
          md4
          lg4
          xl4
        >
          <v-card
            class="mx-auto"
            :class="$vuetify.theme.isDark ? 'light-shade--text' : 'text--text'"
            width="20rem"
            :color="$vuetify.theme.isDark ? 'light-accent' : ''"
            rounded
          >
            <div class="pt-2">
              <v-img
                :src="require(`../${card.src}.${card.ext || 'svg'}`)"
                class="align-end mx-auto "
                width="10rem"
              />
            </div>
            <v-card-title>{{ card.title }}</v-card-title>

            <v-card-text
              :class="
                $vuetify.theme.isDark ? 'light-shade--text' : 'text--text'
              "
              v-text="card.text"
            >
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn
                rounded
                large
                class="px-5"
                :color="$vuetify.theme.isDark ? 'dark-shade' : 'secondary'"
                :class="
                  $vuetify.theme.isDark ? 'light-shade--text' : 'text--text'
                "
                :href="card.links[0].link"
                target="_blank"
              >
                {{ card.links[0].text }}
              </v-btn>
              <v-spacer></v-spacer>
            </v-card-actions>
          </v-card>
        </v-col>
      </v-row>
    </v-layout>
  </v-container>
</template>

<script>
import { mapState } from "vuex";
import numeral from "numeral";

export default {
  name: "Information",
  computed: {
    ...mapState(["appState", "locale", "text", "totals"]),
    getTotals() {
      const totals = {
        positivo: numeral(this.totals.mx.positivo).format("0,0"),
        finado: numeral(this.totals.mx.finado).format("0,0"),
      };
      return totals;
    },
  },
};
</script>

<style lang="scss" scoped>
.card-img {
  padding-top: 0.5rem;
  width: 40%;
  height: 0%;
  max-width: 200px;
  max-height: 200px;
}

.md-card {
  max-width: 450px;
  margin-top: 1rem;
  padding-top: 1rem;
  padding-bottom: 1rem;
}
</style>
