<template>
  <div>
    <selection-buttons />
    <v-container fluid>
      <v-col cols="12" align="end">
        <v-btn
          x-large
          fab
          :color="$vuetify.theme.isDark ? 'dark-shade' : 'secondary'"
          :class="$vuetify.theme.isDark ? 'light-shade--text' : 'text--text'"
          @click.prevent="toggleGraphState()"
        >
          <v-icon>{{ btnIcon }}</v-icon>
        </v-btn>
      </v-col>
      <v-row
        v-if="
          (appState === 'loaded' &&
            graphType === 'barRace' &&
            graphState === 'running') ||
            graphState === 'ended' ||
            graphState === 'stopped'
        "
        cols="12"
      >
        <v-col
          cols="12"
          v-if="
            (appState === 'loaded' && graphState === 'running') ||
              graphState === 'ended'
          "
        >
          <v-sheet
            rounded
            color="chart-background"
            class="elevation-2"
            :key="componentKey"
          >
            <div id="barRaceGroup">
              <bar-chart-race
                ref="barRaceElem"
                barChartName="barRaceDiv"
                :viewerState="graphState"
                :data="stateTrends"
                :width="graphWidth"
                :height="$screen.height / 1.15"
                :valueType="countType"
                :key="componentKey + 1"
              ></bar-chart-race>
            </div>
          </v-sheet>
        </v-col>
        <v-col v-else cols="12">
          <v-card
            rounded
            color="chart-background"
            class="mx-auto elevation-2"
            @click.prevent="toggleGraphState()"
          >
            <v-icon class="mt-5 display-icon sheet-text" :class="icon.color">{{
              icon.name
            }}</v-icon>
            <v-card-text
              class="display-1"
              :class="$vuetify.theme.isDark ? 'dark-shade--text' : 'text--text'"
              >{{ label }}</v-card-text
            >
            <v-card-text
              class="title"
              :class="$vuetify.theme.isDark ? 'dark-shade--text' : 'text--text'"
              >{{ description }}</v-card-text
            >
          </v-card>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>
// @ is an alias to /src
import { mapState } from "vuex";
import moment from "moment";
import SelectionButtons from "@/components/SelectionButtons";
import BarChartRace from "@/components/BarChartRace.vue";
import EmptyState from "@/components/EmptyState.vue";

export default {
  name: "graph-viewer",
  components: {
    SelectionButtons,
    BarChartRace,
    // EmptyState,
  },
  data() {
    return {
      graphType: "barRace",
      componentKey: 0,
      title: "Entidades más afectadas",
    };
  },
  computed: {
    ...mapState([
      "appState",
      "trends",
      "dateRange",
      "entidades",
      "graphs",
      "totals",
      "caseType",
      "countType",
    ]),
    graphState() {
      return this.graphs.barRace.state;
    },
    graphWidth() {
      let graphWidth = this.$screen.width / 1.3;
      if (this.$screen.breakpoint === "xs") {
        graphWidth = this.$screen.width / 1.1;
      } else if (this.$screen.breakpoint === "sm") {
        graphWidth = this.$screen.width / 1.08;
      } else if (this.$screen.breakpoint === "md") {
        graphWidth = this.$screen.width / 1.07;
      } else if (this.$screen.breakpoint === "lg") {
        graphWidth = this.$screen.width / 1.25;
      } else if (this.$screen.breakpoint === "xl") {
        graphWidth = this.$screen.width / 1.2;
      }
      return graphWidth;
    },
    stateTrends() {
      const trendData = [];
      const startDate = moment(this.dateRange.start);
      const stopDate = moment(this.dateRange.stop);
      const daysRange = stopDate.diff(startDate, "days");
      const stateCounts = {};
      for (let d = 0; d <= daysRange; d++) {
        const date = moment(this.dateRange.start)
          .add(d, "days")
          .format("YYYY-MM-DD");
        for (let i = 1; i <= 32; i++) {
          const id = `${i}`;
          const name = this.entidades[id].name;
          const cases = this.trends.entidades[name][this.caseType][d];
          if (this.countType === "percent") {
            const { positivo, negativo } = this.totals.entidades[name];
            const pruebasTotales = positivo + negativo;
            const denominador = pruebasTotales > 0 ? pruebasTotales : 1;
            const percent = cases / denominador;
            stateCounts[name] = percent;
          } else {
            stateCounts[name] = cases;
          }
        }
        const dataPoint = { date, ...stateCounts };
        trendData.push(dataPoint);
      }

      return trendData.sort((a, b) =>
        new Date(a.date) < new Date(b.date) ? -1 : 1
      );
    },
    startDate() {
      return moment(this.dateRange.min)
        .locale("es")
        .format("L");
    },
    stopDate() {
      return moment(this.dateRange.max)
        .locale("es")
        .format("L");
    },
    icon() {
      let icon = "chart-bar";
      if (this.caseType === "positivo" || this.countType === "numbers") {
        icon = `chart-bar`;
      }

      return {
        name: `fas fa-${icon}`,
        color: this.caseType === "positivo" ? "warning--text" : "red--text",
      };
    },
    btnIcon() {
      let icon = "play";
      if (this.graphState === "stopped") {
        icon = `play`;
      } else if (this.graphState === "running") {
        icon = `stop`;
      } else if (this.graphState === "ended") {
        icon = `redo-alt`;
      }
      return `fas fa-${icon}`;
    },
    label() {
      let label = "Casos reportados";
      if (this.caseType === "positivo") {
        label = `Casos positivos${this.countType === "percent" ? " %" : ""}`;
      } else if (this.caseType === "finado") {
        label = `Defunciones${this.countType === "percent" ? " %" : ""}`;
      }
      return label;
    },
    description() {
      let description = "";
      if (this.caseType === "positivo" && this.countType === "numbers") {
        description = `Número de casos positivos por entidad federativa entre ${this.startDate} y ${this.stopDate}`;
      } else if (this.caseType === "positivo" && this.countType === "percent") {
        description = `Porcentaje de casos positivos por entidad federativa entre ${this.startDate} y ${this.stopDate}`;
      } else if (this.caseType === "finado" && this.countType === "numbers") {
        description = `Número de casos finados por entidad federativa entre ${this.startDate} y ${this.stopDate}`;
      } else if (this.caseType === "finado" && this.countType === "percent") {
        description = `Porcentaje de defunciones por entidad federativa entre ${this.startDate} y ${this.stopDate}`;
      }
      return description;
    },
  },
  methods: {
    toggleGraphState() {
      if (this.graphState === "running") {
        this.$store.commit("SET_BAR_RACE_STATE", "stopped");
        this.$refs.barRaceElem.stopAnimation();
        this.forceRerender();
      } else {
        this.$store.commit("SET_BAR_RACE_STATE", "running");
        this.$store.commit("SET_BAR_RACE_DESC", this.description);
        //this.$refs.barRaceElem.mountBarChartRace(this.stateTrends);
        this.forceRerender();
      }
    },
    forceRerender() {
      this.componentKey++;
    },
    setTitle(height) {
      this.title = `${height} Entidades más afectadas`;
    },
  },
  watch: {
    caseType() {
      if (this.graphState === "running") {
        this.$store.commit("SET_BAR_RACE_STATE", "ended");
      }
    },
    countType() {
      if (this.graphState === "running") {
        this.$store.commit("SET_BAR_RACE_STATE", "ended");
      }
    },
    "$screen.width"() {
      this.$store.commit("SET_BAR_RACE_STATE", "ended");
      //this.forceRerender();
    },
    "$screen.height"() {
      this.$store.commit("SET_BAR_RACE_STATE", "ended");
      //this.forceRerender();
    },
  },
};
</script>

<style lang="scss" scoped>
.title {
  width: 100%;
}

.display-icon {
  font-size: 15rem;
}
.text-icon {
  margin-top: -0.5rem;
}
</style>
