import * as d3 from "d3";

const localeMx = {
  decimal: ".",
  thousands: ",",
  grouping: [3],
  currency: ["$", ""],
  dateTime: "%a %b %e %X %Y",
  date: "%d/%m/%Y",
  time: "%H:%M:%S",
  periods: ["AM", "PM"],
  days: [
    "Domingo",
    "Lunes",
    "Martes",
    "Miércoles",
    "Jueves",
    "Viernes",
    "Sábado",
  ],
  shortDays: ["Dom", "Lun", "Mar", "Mi", "Jue", "Vie", "Sab"],
  months: [
    "Enero",
    "Febrero",
    "Marzo",
    "Abril",
    "Mayo",
    "Junio",
    "Julio",
    "Agosto",
    "Septiembre",
    "Octubre",
    "Noviembre",
    "Diciembre",
  ],
  shortMonths: [
    "Ene",
    "Feb",
    "Mar",
    "Abr",
    "May",
    "Jun",
    "Jul",
    "Ago",
    "Sep",
    "Oct",
    "Nov",
    "Dic",
  ],
};

d3.timeFormatDefaultLocale(localeMx);

export function createBarChartRace(chartDivId, srcData, options) {
  const data = srcData;
  const chartDiv = document.getElementById(`${chartDivId}`);
  if (!chartDiv) {
    return;
  }

  chartDiv.textContent = "";
  const timeIndex = options.timeIndex || 10;
  const columnNames = options.columnNames || [];
  const topN = options.topN || 10;
  const width = options.width;
  const height = options.height - 50;
  const valueType = options.valueType;

  const durationSec =
    options.durationSec && options.durationSec > 0 ? options.durationSec : 20;
  const tickduration = (durationSec / data.length) * 1000;

  const svg = d3
    .select(chartDiv)
    .append("svg")
    .attr("width", width)
    .attr("height", height);

  const timelineSvg = d3
    .select(chartDiv)
    .append("svg")
    .attr("width", width)
    .attr("height", 50);

  const margin = {
    top: 20,
    right: 80,
    bottom: 0,
    left: 0,
  };

  const marginTimeAxis = 30;

  const barPadding = (height - (margin.bottom + margin.top)) / (topN * 5);

  function getRowData(data, columnNames, rowIndex) {
    const row = data[rowIndex];
    let newData = columnNames.map((name) => {
      return { name: name, value: row[name] };
    });
    newData = newData.sort((a, b) => b.value - a.value).slice(0, topN);
    newData.forEach((d, i) => {
      d.rank = i;
      d.lastValue = rowIndex > 0 ? data[rowIndex - 1][d.name] : d.value;
    });
    return [row.date, newData];
  }

  // define a random color for each column
  const colors = {};
  const colorScale = d3.scaleOrdinal(d3.schemeSet3);

  columnNames.forEach((name, i) => {
    colors[name] = colorScale(i);
  });

  // Parse data
  data.forEach((d) => {
    // first column : YYYY-MM-DD
    if (typeof d[timeIndex] === "string") {
      const parseTime = d3.timeParse("%Y-%m-%d");
      d[timeIndex] = parseTime(d[timeIndex]);
    }
    // convert other columns to numbers
    columnNames.forEach((k) => (d[k] = Number(d[k])));
  });

  // draw the first frame
  let [time, rowData] = getRowData(data, columnNames, 0);
  const startDate = d3.min(data, (d) => d[timeIndex]);
  const endDate = d3.max(data, (d) => d[timeIndex]);

  const t = d3
    .scaleTime()
    .domain([startDate, endDate])
    .range([margin.left + marginTimeAxis, width - margin.right]);

  const timeAxis = d3
    .axisBottom()
    .ticks(5)
    .scale(t);

  const x = d3
    .scaleLinear()
    .domain([0, d3.max(rowData, (d) => d.value)])
    .range([margin.left, width - margin.right]);

  const y = d3
    .scaleLinear()
    .domain([topN, 0])
    .range([height - margin.bottom, margin.top]);

  const xAxis = d3
    .axisTop()
    .scale(x)
    .ticks(5)
    .tickSize(-(height - margin.top - margin.bottom))
    .tickFormat((d) => d3.format(",")(d));

  svg
    .append("g")
    .attr("class", "axis xAxis")
    .attr("transform", `translate(0, ${margin.top})`)
    .call(xAxis)
    .selectAll(".tick line")
    .classed("origin", (d) => d === 0);

  svg
    .selectAll("rect.bar")
    .data(rowData, (d) => d.name)
    .enter()
    .append("rect")
    .attr("class", "bar")
    .attr("x", x(0) + 1)
    .attr("width", (d) => x(d.value) - x(0))
    .attr("y", (d) => y(d.rank) + barPadding / 2)
    .attr("height", y(1) - y(0) - barPadding)
    .style("fill", (d) => colors[d.name]);

  svg
    .selectAll("text.label")
    .data(rowData, (d) => d.name)
    .enter()
    .append("text")
    .attr("class", "label")
    .attr("x", (d) => x(d.value) - 8)
    .attr("y", (d) => y(d.rank) + (y(1) - y(0)) / 2 + 1)
    .style("text-anchor", "end")
    .html((d) => d.name);

  svg
    .selectAll("text.valueLabel")
    .data(rowData, (d) => d.name)
    .enter()
    .append("text")
    .attr("class", "valueLabel")
    .attr("x", (d) => x(d.value) + 5)
    .attr("y", (d) => y(d.rank) + (y(1) - y(0)) / 2 + 1)
    .text((d) => {
      return d3.format(",.0f")(d.lastValue);
    });

  // svg.append('rect')
  //     .attr('y', height - margin.bottom)
  //     .attr('width', width)
  //     .attr('height', margin.bottom)
  //     .style('fill', '#ffffff')

  timelineSvg
    .append("g")
    .attr("class", "axis tAxis")
    .attr("transform", `translate(0, 20)`)
    .call(timeAxis);

  timelineSvg
    .append("rect")
    .attr("class", "progressBar")
    .attr("transform", `translate(${marginTimeAxis}, 20)`)
    .attr("height", 2)
    .attr("width", 0);

  const timeText = svg
    .append("text")
    .attr("class", "timeText")
    .attr("x", width - margin.right)
    .attr("y", height - margin.bottom - 5)
    .style("text-anchor", "end")
    .html(() => {
      return d3.timeFormat("%B %d, %Y")(time);
    });

  // draw the updated graph with transitions
  function drawGraph() {
    // update xAxis with new domain
    x.domain([0, d3.max(rowData, (d) => d.value)]);
    svg
      .select(".xAxis")
      .transition()
      .duration(tickduration)
      .ease(d3.easeLinear)
      .call(xAxis);

    // update bars
    const bars = svg.selectAll(".bar").data(rowData, (d) => d.name);

    bars
      .enter()
      .append("rect")
      .attr("class", "bar")
      .attr("x", x(0) + 1)
      .attr("width", (d) => x(d.value) - x(0))
      //enter from out of screen
      .attr("y", (d) => y(topN + 1) + 0)
      .attr("height", y(1) - y(0) - barPadding)
      .style("fill", (d) => colors[d.name])
      .transition()
      .duration(tickduration)
      .ease(d3.easeLinear)
      .attr("y", (d) => y(d.rank) + barPadding / 2);

    bars
      .transition()
      .duration(tickduration)
      .ease(d3.easeLinear)
      .attr("width", (d) => x(d.value) - x(0))
      .attr("y", (d) => y(d.rank) + barPadding / 2);

    bars
      .exit()
      .transition()
      .duration(tickduration)
      .ease(d3.easeLinear)
      .attr("width", (d) => x(d.value) - x(0))
      .attr("y", (d) => y(topN + 1) + barPadding / 2)
      .remove();

    // update labels
    const labels = svg.selectAll(".label").data(rowData, (d) => {
      if (d.name === "Veracruz De Ignacio De La Llave") {
        return "Veracruz";
      } else if (d.name === "Coahuila De Zaragoza") {
        return "Coahuila";
      } else if (d.name === "Michoacán De Ocampo") {
        return "Michoacán";
      } else {
        return d.name;
      }
    });

    labels
      .enter()
      .append("text")
      .attr("class", "label")
      .attr("x", (d) => x(d.value) - 8)
      .attr("y", (d) => y(topN + 1) + (y(1) - y(0)) / 2)
      .style("text-anchor", "end")
      .html((d) => {
        if (d.name === "Veracruz De Ignacio De La Llave") {
          return "Veracruz";
        } else if (d.name === "Coahuila De Zaragoza") {
          return "Coahuila";
        } else if (d.name === "Michoacán De Ocampo") {
          return "Michoacán";
        } else {
          return d.name;
        }
      })
      .transition()
      .duration(tickduration)
      .ease(d3.easeLinear)
      .attr("y", (d) => y(d.rank) + (y(1) - y(0)) / 2 + 1);

    labels
      .transition()
      .duration(tickduration)
      .ease(d3.easeLinear)
      .attr("x", (d) => x(d.value) - 8)
      .attr("y", (d) => y(d.rank) + (y(1) - y(0)) / 2 + 1);

    labels
      .exit()
      .transition()
      .duration(tickduration)
      .ease(d3.easeLinear)
      .attr("x", (d) => x(d.value) - 8)
      .attr("y", (d) => y(topN + 1))
      .remove();

    // update value labels

    const valueLabels = svg
      .selectAll(".valueLabel")
      .data(rowData, (d) => d.name);

    valueLabels
      .enter()
      .append("text")
      .attr("class", "valueLabel")
      .attr("x", (d) => x(d.value) + 5)
      .attr("y", (d) => y(topN + 1))
      .text((d) => {
        if (valueType === "percent") {
          return d3.format(",.1%")(d.lastValue);
        } else {
          return d3.format(",.0f")(d.lastValue);
        }
      })
      .transition()
      .duration(tickduration)
      .ease(d3.easeLinear)
      .attr("y", (d) => y(d.rank) + (y(1) - y(0)) / 2 + 1);

    valueLabels
      .transition()
      .duration(tickduration)
      .ease(d3.easeLinear)
      .attr("x", (d) => x(d.value) + 5)
      .attr("y", (d) => y(d.rank) + (y(1) - y(0)) / 2 + 1)
      .tween("text", function(d) {
        const i = d3.interpolateNumber(d.lastValue, d.value);
        return function(t) {
          if (valueType === "percent") {
            this.textContent = d3.format(",.1%")(i(t));
          } else {
            this.textContent = d3.format(",.0f")(i(t));
          }
        };
      });

    valueLabels
      .exit()
      .transition()
      .duration(tickduration)
      .ease(d3.easeLinear)
      .attr("x", (d) => x(d.value) + 5)
      .attr("y", (d) => y(topN + 1))
      .remove();

    // update time label and progress bar
    d3.select(".progressBar")
      .transition()
      .duration(tickduration)
      .ease(d3.easeLinear)
      .attr("width", t(time) - marginTimeAxis);
    // .on('end', () => {
    //     d3.select('.timeText').html(d3.timeFormat("%B %d, %Y")(time))
    // timeText.html(d3.timeFormat("%B %d, %Y")(time))
    // })
    timeText.html(() => {
      return d3.timeFormat("%B %d, %Y")(time);
    });
  }

  // loop
  let i = 1;
  const interval = d3.interval((e) => {
    [time, rowData] = getRowData(data, columnNames, i);
    drawGraph();
    // increment loop
    i++;
    if (i >= data.length) {
      interval.stop();
      if (options.stopFunc) {
        options.stopFunc("ended");
      }
    }
  }, tickduration);
  return interval;
}
